.tooltip {
position: absolute;
z-index: 1070;
display: block;
font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
font-style: normal;
font-weight: normal;
letter-spacing: normal;
line-break: auto;
line-height: 1.42857143;
text-align: left;
text-align: start;
text-decoration: none;
text-shadow: none;
text-transform: none;
white-space: normal;
word-break: normal;
word-spacing: normal;
word-wrap: normal;
font-size: 12px;
opacity: 0;
filter: alpha(opacity=0);
}

.tooltip.in {
opacity: 0.9;
filter: alpha(opacity=90);
}

.tooltip.top {
margin-top: -3px;
padding: 5px 0;
min-width: 120px;
}

.tooltip.right {
margin-left: 3px;
padding: 0 5px;
}

.tooltip.bottom {
margin-top: 3px;
padding: 5px 0;
}

.tooltip.left {
margin-left: -3px;
padding: 0 5px;
}

.tooltip-inner {
max-width: 200px;
padding: 3px 8px;
color: #ffffff;
text-align: center;
background-color: #005596;
border-radius: 4px;
}

.tooltip-arrow {
position: absolute;
width: 0;
height: 0;
border-color: transparent;
